9px;\n  color: #314e52;\n  font-style: normal;\n  font-weight: 600;\n  font-size: 14px;\n  z-index: 1;\n}\n.hexagon:before,\n.hexagon:after {\n  position: absolute;\n  content: \"\";\n  width: 255px;\n  left: 0px;\n  height: 27px;\n  z-index: -1;\n}\n\n.hexagon:before {\n  transform: perspective(12px) rotateX(3deg);\n}\n.hexagon:after {\n  top: 33px;\n  transform: perspective(12px) rotateX(-3deg);\n}\n\n/* Button Border Style */\n\n.hexagon.border:before,\n.hexagon.border:after {\n  border: 4px solid #f2a154;\n  background: #f2a154;\n}\n.hexagon.border:before {\n  border-bottom: none; /* to prevent the border-line showing up in the middle of the shape */\n}\n.hexagon.border:after {\n  border-top: none; /* to prevent the border-line showing up in the middle of the shape */\n}\n\n/* Button hover styles */\n\n.hexagon.border:hover:before,\n.hexagon.border:hover:after {\n  background: #f2a154;\n}\n.hexagon.border:hover {\n  color: #f7f6e7;\n}\n\n:root {\n  --border-size: 0.025rem;\n  --duration: 250ms;\n  --ease: cubic-bezier(0.215, 0.61, 0.355, 1);\n  --font-family: krub;\n  --color-primary: #f2a154;\n  --color-secondary: #314e52;\n  --color-tertiary: #314e52;\n  --shadow: rgba(0, 0, 0, 0.07);\n  --space: 0.2rem;\n}\n\n* {\n  box-sizing: border-box;\n}\n\n.multi-button {\n  display: flex;\n  width: 70%;\n  height: 61%;\n  box-shadow: 0px 1.5px 4px var(--shadow);\n}\n\n.multi-button button {\n  flex-grow: 1;\n  cursor: pointer;\n  position: relative;\n  padding: calc(var(--space) / 1.125) var(--space) var(--space);\n  border: var(--border-size) solid black;\n  color: var(--color-secondary);\n  background-color: var(--color-primary);\n  font-size: 0.75rem;\n  font-weight: 500;\n  font-family: var(--font-family);\n  text-transform: lowercase;\n  text-shadow: 0px 1.5px 4px var(--shadow);\n  transition: flex-grow var(--duration) var(--ease);\n}\n\n.multi-button button + button {\n  border-left: var(--border-size) solid black;\n  margin-left: calc(var(--border-size) * -1);\n}\n\n.multi-button button:hover,\n.multi-button button:focus {\n  flex-grow: 2;\n  color: #f2a154;\n  outline: none;\n  text-shadow: none;\n  background-color: var(--color-secondary);\n}\n\n.multi-button button:focus {\n  outline: var(--border-size);\n  outline-offset: calc(var(--border-size) * -3);\n}\n\n.multi-button:hover button:focus:not(:hover) {\n  flex-grow: 1;\n  color: var(--color-secondary);\n  background-color: var(--color-primary);\n  outline-color: var(--color-tertiary);\n}\n\n.multi-button button:active {\n  transform: translateY(var(--border-size));\n}\n\n@media only screen and (min-device-width: 960px) and (max-device-width: 1280px) and (-webkit-min-device-pixel-ratio: 1) {\n  /* developer note 768 - 1300 */\n  .container {\n    grid-template-rows: 70px 65px 220px 1fr;\n  }\n\n  #lb-pic-middle {\n    width: 70%;\n  }\n\n  #lb-pic-big {\n    width: 70%;\n  }\n\n  .lb-all {\n    padding-top: 120px;\n  }\n\n  #lb-top-left {\n    grid-column: 4 / 7;\n    transform: translateX(188px) translateY(100px);\n  }\n\n  #lb-top-big {\n    grid-column: 4 / 7;\n  }\n\n  #lb-top-right {\n    grid-column: 4 / 7;\n    transform: translateX(-188px) translateY(100px);\n  }\n\n  .lb-honeycomb {\n    justify-self: center;\n    grid-column: 4 / 7;\n    grid-row: 3 / 4;\n    position: relative;\n    width: 200px;\n    height: 115.47px;\n    background-color: #f2a154;\n    margin: 57.74px 0;\n    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);\n  }\n\n  #lb-honeycomb-left {\n    transform: translateX(-187px) translateY(95px) rotate(30deg);\n  }\n\n  #lb-honeycomb-middle {\n    transform: translateY(-10px) rotate(30deg);\n  }\n\n  #lb-honeycomb-right {\n    transform: translateX(187px) translateY(95px) rotate(30deg);\n  }\n\n  .lb-honeycomb:before,\n  .lb-honeycomb:after {\n    content: \"\";\n    position: absolute;\n    z-index: 1;\n    width: 141.42px;\n    height: 141.42px;\n    -webkit-transform: scaleY(0.5774) rotate(-45deg);\n    -ms-transform: scaleY(0.5774) rotate(-45deg);\